import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import $ from 'jquery'
import 'jstree/dist/jstree.min'

const ROOT_ID = 'root'
export default class ContentTreeView extends PureComponent {
  constructor (props) {
    super(props)
    this.tree = null
    this.treeRootLocationId = props.treeRootLocationId
  }

  getTreeData () {
    const data = [
      {
        id: ROOT_ID,
        text: Translator.trans('menu.root'),
        parent: '#',
        type: ROOT_ID,
        state: {
          opened: true
        }
      }
    ]

    return data
  }

  handleCheck (operation, node, parentNode, nodePosition, more) {
    if (operation === 'move_node') {
      if (node.id === ROOT_ID) {
        return false
      }
      if (parentNode.id === '#') {
        return false
      }
      if (node.state.disabled) {
        return false
      }
    }
    return true
  }

  getNode (id) {
    return this.tree.get_node(id)
  }

  componentDidMount () {
    this.tree = $(this.treeContainer)
      .jstree({
        core: {
          data: this.getTreeData(),
          check_callback: this.handleCheck
        },
        conditionalselect: (node, event) => {
          return false
        },
        plugins: ['changed', 'dnd', 'conditionalselect', 'types']
      })
      .jstree(true)
    // $(this.treeContainer).on('move_node.jstree copy_node.jstree delete_node.jstree create_node.jstree', this.onTreeChange)
  }

  componentDidUpdate () {
    this.tree.settings.core.data = this.getTreeData()
    this.tree.refresh({ skip_loading: true })
  }

  render () {
    return <div ref={(div) => (this.treeContainer = div)} />
  }
}

ContentTreeView.propTypes = {
  treeRootLocationId: PropTypes.number
}



